import React from 'react';
import './ServiceItem.css'

interface ServiceItemProps {
  image: string;
  title: string;
  icon: string;
  text: string;
  quote: string;
}

function ServiceItem(props: ServiceItemProps) {
  return (
    <div className="ServiceItem">
      <div className="ServiceItemImage">
        <img src={props.image} alt="Service" />
      </div>
      <div className="ServiceItemContent">
        <div className="ServiceItemTitle">
          {props.title}
        </div>
        <div className="ServiceItemDetails">
          <div className="ServiceItemIcon">
            <i className={props.icon} />
          </div>
          <div className="ServiceItemText">
            {props.text}
          </div>
        </div>
        <div className="ServiceItemQuote">
          {props.quote}
        </div>
      </div>
    </div>
  );
}

export default ServiceItem;
